/*Contact Form section styling start */
.form_col {
  padding-right: 30px;
}
section.contact_form {
  padding: 7% 0%;
}
.form-container {
  width: 100%;
}
.contact-form_heading {
  font-size: 28px;
  font-weight: 800;
  line-height: 33.6px;
  text-align: left;
  margin-bottom: 6%;
}
form {
  display: flex;
  flex-direction: column;
}

.form-row {
  display: flex;
  justify-content: space-between;
}

.form-group {
  flex: 1;
  margin-bottom: 15px;
  position: relative;
}

.form-group label {
  position: absolute;
  top: -10px;
  left: 10px;
  background-color: black;
  padding: 0 5px;
  font-size: 14px;
  color: white;
}
input.userInfo_group {
  width: 100%;
  height: 67px;
  border-radius: 12px;
  color: #A3A09E;
  font-size: 16px;
  font-weight: 500;
  line-height: 19.36px;
  text-align: left;
  font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif;
}
input, textarea, select {
  width: 100%;
  padding: 10px 20px;
  background-color: transparent;
  border: 1px solid #fff;
  color: white;
}

input::placeholder, textarea::placeholder {
  color: #ccc;
}
textarea.message_group {
  width: 100%;
  height: 137.5px;
  border-radius: 12px;
  color: #A3A09E;
  font-size: 16px;
  font-weight: 500;
  line-height: 19.36px;
  text-align: left;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

.submit-btn {
  height: 60px;
  padding: 12px 28px;
  background-color: #F4A70B;
  color: #100F0E;
  border: none;
  cursor: pointer;
  border-radius: 12px;
  font-size: 16px;
  font-family: basementgrotesque;
  font-size: 16px;
  font-weight: 800;
  line-height: 25.6px;
}
.submit-btn:hover {
  background-color: #ffaa00;
}

@media (max-width: 768px) {
  .form-row {
    flex-direction: column;
  }

  .form-group {
    margin-bottom: 10px;
  }
}
.form-group {
  position: relative;
  margin: 0% 0.5% 4% 0.5%;
}
.form-group label {
  font-size: 16px;
  color: #fff;
  position: absolute;
  top: -10px;
  left: 15px;
  background-color: hsl(30, 18%, 7%);
  padding: 0 5px;
  z-index: 1;
  font-size: 12px;
  font-weight: 500;
  line-height: 14.52px;
  text-align: left;
  font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif;
}

/* select section styling start */

.custom-select-container {
  position: relative;
  width: 100%;
  font-family: Arial, sans-serif;
}
.custom-label {
  color: #fff;
  position: absolute;
  top: -10px;
  left: 10px;
  background-color: hsl(30, 18%, 7%);
  padding: 0 5px;
  z-index: 1;
  font-size: 12px;
  font-weight: 500;
  line-height: 14.52px;
  text-align: left;
  font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif;
}
.custom-select {
  background-color: transparent;
  color: #fff;
  border: 1px solid #fff;
  padding: 10px 10px 10px 20px;
  border-radius: 12px;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  width: 100%;
  transition: border-color 0.3s;
  height: 67px;
}
.selected-option {
  flex-grow: 1;
  color: #A3A09E;
  font-size: 16px;
  font-weight: 500;
  line-height: 19.36px;
  text-align: left;
  font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif;
}
.dropdown-icon {
  margin-left: 10px;
  width: 15px;
  height: 12px;
  cursor: pointer;
}
.select_dropdown li {
  color: #5E5C5B;
  list-style: none;
  padding: 20px 20px;
  cursor: pointer;
  font-size: 15px;
  font-weight: 400;
  line-height: 14.52px;
  text-align: left;
  transition: background-color 0.3s;
  font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif;
}
.select_dropdown li:last-child {
  border-bottom: none;
}
.custom-select.active + .select_dropdown {
  display: block;
}
.custom-select-container {
  position: relative;
  width: 100%;
}
.select_dropdown {
  display: block;
  position: absolute;
  top: 69%;
  left: 0;
  background-color: #fff;
  width: 100%;
  z-index: 999;
  border-radius: 12px;
  padding: 0px;
}
.select_dropdown li:hover {
  color: #F34900;
  background-color: #F4A70B33;
  font-weight: 700;
}
  /* select section styling end */
